.content{
	align-items: unset;
	width: 100%;
    margin-bottom: 15px;
}

table {
    font-size: 22px;
    border-radius: 10px;
    border-spacing: 0;
    text-align: center;
}

th {
    background: #e198ffab;
    padding: 7px 15px;
    width: 20%;
}

th, td {
    border-style: solid;
    color: #434449f5;
    border-width: 0 1px 1px 0;
    border-color: white;
}

th:first-child {
    border-top-left-radius: 10px;
}

th:last-child {
    border-top-right-radius: 10px;
    border-right: none;
}

td {
    padding: 6px 20px;
    background: #ebf749ab;
}

tr:last-child td:first-child {
    border-radius: 0 0 0 10px;
}

tr:last-child td:last-child {
    border-radius: 0 0 10px 0;
}

tr td:last-child {
    border-right: none;
}

.info{
    text-align: center;
    font-size: 30px;
    margin-top: 70px;
    margin-bottom: 70px;
    color: rgb(58, 24, 66);
}
.subtitle{
	text-align: right;
	font-size: 15px;
	margin-top: 7px;
}